import React, { cloneElement } from 'react';
import { useFormContext } from 'react-hook-form';
import { styled } from 'styled-components';

import LoadingEllipsis from '#components/LoadingEllipsis';

import { InputState } from '../Input.types';
import FormInputWrapper from '../InputWrapper';

import { Input, InputButton } from './TextInput.styles';
import { TextInputProps } from './TextInput.types';

import useInputButton from '#hooks/useInputButton';

const LoadingEllipsisContainer = styled.div`
  position: absolute;
  top: 50%;
  right: ${({ theme }) => theme.spacing.x2}px;
  transform: translateY(-50%);
`;

const TextInput: React.FC<TextInputProps> = ({
  id,
  label,
  helpText,
  className,
  message,
  type = 'text',
  state,
  button,
  buttonAction,
  controlled = true,
  required = false,
  ...inputProps
}) => {
  const form = useFormContext();

  if (controlled && !form) {
    throw new Error(
      `FormInputs should be wrapped by a FormProvider from @indy/ui/Form, unless the controlled={false} prop is set`,
    );
  }

  // Check for context so we can use this input as a
  // standalone component without react-hook-form
  const inputRegistration = controlled ? form.register(id) : {};

  const onAction = () => {
    buttonAction && buttonAction();
  };

  const buttonProps = useInputButton(onAction);

  const createInputButton = () =>
    button ? cloneElement(button, buttonProps) : null;

  return (
    <FormInputWrapper
      id={id}
      label={label}
      helpText={helpText}
      required={required}
      state={state}
      message={message}
      className={className}
    >
      <Input
        id={id}
        type={type}
        $hasButton={!!button}
        {...inputProps}
        {...inputRegistration}
      ></Input>
      {state === InputState.WAITING && (
        <LoadingEllipsisContainer>
          <LoadingEllipsis />
        </LoadingEllipsisContainer>
      )}
      {button && state !== InputState.WAITING && (
        <InputButton>{createInputButton()}</InputButton>
      )}
    </FormInputWrapper>
  );
};

export default TextInput;
