const KEY_CODES = {
  Enter: 'Enter',
  Space: ' ',
  Tab: 'Tab',
};

const MOUSE_CODES = {
  Left: 0,
};

/*
 * Hook to return to add interactivity to an input button
 * (e.g. show/hide password button) without interrupting default
 * focus and tabbing behaviour
 *
 * Used in the TextInput component and utilised by PasswordInput
 *
 */
const useInputButton = (action: VoidFunction) => {
  // NOTE: We use onMouseDown instead of onClick to prevent
  // inputs from losing focus when the button is clicked
  const onMouseDown = (evt: MouseEvent) => {
    // Prevent focus from being taken away from the input
    evt.preventDefault();

    if (evt.button === MOUSE_CODES.Left) {
      action();
    }
  };

  const onKeyDown = (evt: KeyboardEvent) => {
    if (evt.key === KEY_CODES.Enter || evt.key === KEY_CODES.Space) {
      // Prevent space being added to the input value
      // NOTE: This needs to happen *inside* the if statement so we
      // don't interrupt default tabbing behaviour
      evt.preventDefault();
      action();
    }
  };

  return { onMouseDown, onKeyDown };
};

export default useInputButton;
