import { styled } from 'styled-components';

import { BaseInput, getBorderStyle } from '../Input.styles';
import { InputState, StyledInputProps } from '../Input.types';

export const Input = styled(BaseInput)<StyledInputProps>`
  /**
   * We need to make space for the input button if it exists,
   * to prevent input text overlapping the button
  */
  ${({ theme, $hasButton }) => {
    if ($hasButton) {
      return `padding-right: ${theme.dimension.btn.height}px`;
    }

    return '';
  }}
`;

export const InputButton = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  border-radius: var(--border-radius);
  outline: none;

  &:focus-within {
    outline: ${({ theme }) =>
      getBorderStyle(theme, InputState.DEFAULT, 'focused')};
  }
`;
